@import './mix.less';
body {
    margin: 0;
    padding: 0;
    font-family: "Open Sans";
    ul {
        padding: 0;
        li {
            list-style: none;
        }
    }
    h1,h2,h3,h4,h5 {
        margin: 0;
        padding: 0;
    }
    p {
        margin: 0;
        padding: 0;
    }
    a {
        text-decoration: none;
    }
    .wrapper {
        padding: 10px;
        .item {
            max-width: auto;
            font-size: 15px;
            border: 1px solid #F1F1F1;
            .media( {
                max-width: 1200px;
            }
            , 1249px);
            .card {
                padding: 15px 45px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;
                .media({
                    justify-content: center;
                    padding: 30px 30px;
                }, 690px);
                .image {
                    .media({
                        margin-right: 25px;
                    }, 690px);
                }
                .title {
                    text-align: center;
                    p {
                        .media({
                            margin-bottom: 5px;
                        }, 690px);
                    }
                    .media({
                        text-align: left;
                    }, 690px);
                }
                .coli {
                    display: flex;
                    .media({
                        display: none;
                    }, 690px);
                    &.coli-hide {
                        display: none;
                        .media({
                            display: flex;
                        }, 690px);
                    }
                    .minus {
                        background: #404040;
                        width: 22px;
                        text-align: center;
                        display: flex;
                    }
                    .plus {
                        background: #404040;
                        width: 22px;
                        text-align: center;
                        display: flex;
                    }
                    .fa {
                        font-size: 7px;
                        color: #E7E8EA;
                        margin: auto;
                    }
                    input {
                        margin: 0px 4px;
                        width: 8px;
                        padding: 2px 11px 2px 11px;
                        background: #E7E8EA;
                        border: none;
                    }
                }
                .instock {
                    display: flex;
                    align-items: center;
                    color: #9AC283;
                    .media({
                        display: none;
                    }, 1200px);
                    .media({
                        display: flex;
                        position: absolute;
                        top: 3px;
                        left: 3px;
                    }, 690px);
                    p {
                        border-bottom: 1px dashed #9AC283;
                    }
                }
                .price {
                    text-align: center;
                    .media({
                        display: none;
                    }, 690px);
                }
                .total-price {
                    text-align: center;
                    .media({
                        display: none;
                    }, 1200px);
                }
                .delete {
                    font-size: 30px;
                    color: #E64744;
                    cursor: pointer;
                    .media({
                        display: flex;
                        position: absolute;
                        top: 3px;
                        right: 3px;
                    }, 690px);
                }
            }
        }
        .fast-buy {
            padding: 20px 45px;
            background: #F9F9F9;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .media({
                flex-direction: column;
                justify-content: center;
                margin-bottom: 17px;
            }, 690px);
            .left {
                display: flex;
                align-items: center;
                .circle {
                    height: 48px;
                    width: 48px;
                    border: 1px solid #BABABA;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 25px;
                    .media({
                        display: none;
                    }, 690px);
                    .fa {
                        font-size: 27px;
                        color: #BABABA;
                    }
                }
                .title {
                    .media({
                        text-align: center;
                        margin-bottom: 20px;
                    }, 690px);
                    h5 {
                        .media({
                            text-align: center;
                            margin-bottom: 5px;
                        }, 690px);
                        font-size: 18px;
                    }
                    p {
                        color: #929290;
                    }
                }
            }
            .right {
                button {
                    background: #27C147;
                    border-radius: 2px;
                    border: 0;
                    color: #fff;
                    padding: 7px 25px;
                    width: 142px;
                    cursor: pointer;
                }
            }
        }
        .promo {
            border-top: 1px solid #ECEEED;
            padding: 15px 45px;
            background: #F9F9F9;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .media({
                padding: 30px 45px;
            },1037px);
            .media({
                padding: 15px 25px;
            }, 690px);
            .inf {
                display: flex;
                flex-wrap: wrap;
                p {
                    margin-right: 35px;
                    .media({
                        margin-bottom: 7px;
                    }, 690px);
                }
            }
            .enter {
                max-width: 600px;
                display: flex;
                justify-content: flex-end;
                .media({
                    display: none;
                }, 1037px);
                form {
                    input {
                        border: 1px solid #ECEEED;
                        padding: 15px 0px 15px 5px;
                        width: 380px;
                    }
                    button {
                        background: #2D2D2D;
                        color: #fff;
                        height: 100%;
                        border: 1px solid #2D2D2D;
                        padding: 0px 20px;
                        margin-left: -10px;
                        cursor: pointer;
                    }
                }
            }
        }
        .enter-hide {
            margin-top: 20px;
            width: 100%;
            display: none;
            margin-bottom: 50px;
            .media({
                display: flex;
            },1037px);
            form {
                display: flex;
                width: 100%;
                input {
                    border: 1px solid #ECEEED;
                    padding: 15px 0px 15px 5px;
                    flex-basis: 100%;
                }
                button {
                    background: #2D2D2D;
                    color: #fff;
                    height: 100%;
                    border: 1px solid #2D2D2D;
                    padding: 0px 20px;
                    margin-left: -10px;
                    cursor: pointer;
                }
            }
        }
    }
}