body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans";
}
body ul {
  padding: 0;
}
body ul li {
  list-style: none;
}
body h1,
body h2,
body h3,
body h4,
body h5 {
  margin: 0;
  padding: 0;
}
body p {
  margin: 0;
  padding: 0;
}
body a {
  text-decoration: none;
}
body .wrapper {
  padding: 10px;
}
body .wrapper .item {
  max-width: auto;
  font-size: 15px;
  border: 1px solid #F1F1F1;
}
@media (max-width: 1249px) {
  body .wrapper .item {
    max-width: 1200px;
  }
}
body .wrapper .item .card {
  padding: 15px 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
@media (max-width: 690px) {
  body .wrapper .item .card {
    justify-content: center;
    padding: 30px 30px;
  }
}
@media (max-width: 690px) {
  body .wrapper .item .card .image {
    margin-right: 25px;
  }
}
body .wrapper .item .card .title {
  text-align: center;
}
@media (max-width: 690px) {
  body .wrapper .item .card .title p {
    margin-bottom: 5px;
  }
}
@media (max-width: 690px) {
  body .wrapper .item .card .title {
    text-align: left;
  }
}
body .wrapper .item .card .coli {
  display: flex;
}
@media (max-width: 690px) {
  body .wrapper .item .card .coli {
    display: none;
  }
}
body .wrapper .item .card .coli.coli-hide {
  display: none;
}
@media (max-width: 690px) {
  body .wrapper .item .card .coli.coli-hide {
    display: flex;
  }
}
body .wrapper .item .card .coli .minus {
  background: #404040;
  width: 22px;
  text-align: center;
  display: flex;
}
body .wrapper .item .card .coli .plus {
  background: #404040;
  width: 22px;
  text-align: center;
  display: flex;
}
body .wrapper .item .card .coli .fa {
  font-size: 7px;
  color: #E7E8EA;
  margin: auto;
}
body .wrapper .item .card .coli input {
  margin: 0px 4px;
  width: 8px;
  padding: 2px 11px 2px 11px;
  background: #E7E8EA;
  border: none;
}
body .wrapper .item .card .instock {
  display: flex;
  align-items: center;
  color: #9AC283;
}
@media (max-width: 1200px) {
  body .wrapper .item .card .instock {
    display: none;
  }
}
@media (max-width: 690px) {
  body .wrapper .item .card .instock {
    display: flex;
    position: absolute;
    top: 3px;
    left: 3px;
  }
}
body .wrapper .item .card .instock p {
  border-bottom: 1px dashed #9AC283;
}
body .wrapper .item .card .price {
  text-align: center;
}
@media (max-width: 690px) {
  body .wrapper .item .card .price {
    display: none;
  }
}
body .wrapper .item .card .total-price {
  text-align: center;
}
@media (max-width: 1200px) {
  body .wrapper .item .card .total-price {
    display: none;
  }
}
body .wrapper .item .card .delete {
  font-size: 30px;
  color: #E64744;
  cursor: pointer;
}
@media (max-width: 690px) {
  body .wrapper .item .card .delete {
    display: flex;
    position: absolute;
    top: 3px;
    right: 3px;
  }
}
body .wrapper .fast-buy {
  padding: 20px 45px;
  background: #F9F9F9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 690px) {
  body .wrapper .fast-buy {
    flex-direction: column;
    justify-content: center;
    margin-bottom: 17px;
  }
}
body .wrapper .fast-buy .left {
  display: flex;
  align-items: center;
}
body .wrapper .fast-buy .left .circle {
  height: 48px;
  width: 48px;
  border: 1px solid #BABABA;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 25px;
}
@media (max-width: 690px) {
  body .wrapper .fast-buy .left .circle {
    display: none;
  }
}
body .wrapper .fast-buy .left .circle .fa {
  font-size: 27px;
  color: #BABABA;
}
@media (max-width: 690px) {
  body .wrapper .fast-buy .left .title {
    text-align: center;
    margin-bottom: 20px;
  }
}
body .wrapper .fast-buy .left .title h5 {
  font-size: 18px;
}
@media (max-width: 690px) {
  body .wrapper .fast-buy .left .title h5 {
    text-align: center;
    margin-bottom: 5px;
  }
}
body .wrapper .fast-buy .left .title p {
  color: #929290;
}
body .wrapper .fast-buy .right button {
  background: #27C147;
  border-radius: 2px;
  border: 0;
  color: #fff;
  padding: 7px 25px;
  width: 142px;
  cursor: pointer;
}
body .wrapper .promo {
  border-top: 1px solid #ECEEED;
  padding: 15px 45px;
  background: #F9F9F9;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1037px) {
  body .wrapper .promo {
    padding: 30px 45px;
  }
}
@media (max-width: 690px) {
  body .wrapper .promo {
    padding: 15px 25px;
  }
}
body .wrapper .promo .inf {
  display: flex;
  flex-wrap: wrap;
}
body .wrapper .promo .inf p {
  margin-right: 35px;
}
@media (max-width: 690px) {
  body .wrapper .promo .inf p {
    margin-bottom: 7px;
  }
}
body .wrapper .promo .enter {
  max-width: 600px;
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 1037px) {
  body .wrapper .promo .enter {
    display: none;
  }
}
body .wrapper .promo .enter form input {
  border: 1px solid #ECEEED;
  padding: 15px 0px 15px 5px;
  width: 380px;
}
body .wrapper .promo .enter form button {
  background: #2D2D2D;
  color: #fff;
  height: 100%;
  border: 1px solid #2D2D2D;
  padding: 0px 20px;
  margin-left: -10px;
  cursor: pointer;
}
body .wrapper .enter-hide {
  margin-top: 20px;
  width: 100%;
  display: none;
  margin-bottom: 50px;
}
@media (max-width: 1037px) {
  body .wrapper .enter-hide {
    display: flex;
  }
}
body .wrapper .enter-hide form {
  display: flex;
  width: 100%;
}
body .wrapper .enter-hide form input {
  border: 1px solid #ECEEED;
  padding: 15px 0px 15px 5px;
  flex-basis: 100%;
}
body .wrapper .enter-hide form button {
  background: #2D2D2D;
  color: #fff;
  height: 100%;
  border: 1px solid #2D2D2D;
  padding: 0px 20px;
  margin-left: -10px;
  cursor: pointer;
}
